<template>
    <div class="login_div">
        <button id="wxsys" type="primary" @click="scan()">扫一扫</button>
    </div>
</template>
<script>
    import wx from 'weixin-js-sdk'
    import {SIGN_PACKAGE, BIND, DRIVER_VERIFY, FETCH_OPENID} from '../../common/config'
    import {get, post} from '../../common/http'
    import store from '../../storage'
    import {Toast} from 'vant';

    export default {
        data() {
            return {
                data: {}
            }
        },
        methods: {
            initConfig() {
                get(SIGN_PACKAGE, {}, res => {
                    wx.config({
                        debug: true,
                        appId: res.appId,
                        timestamp: "" + res.timestamp,
                        nonceStr: res.nonceStr,
                        signature: res.signature,
                        jsApiList: ['checkJsApi', 'scanQRCode']
                    });
                })
            },
            scan() {
                wx.checkJsApi({
                    jsApiList: ['scanQRCode'],
                    error: function () {
                        Toast.fail("微信扫一扫初始化失败")
                    }
                });
                wx.scanQRCode({
                    needResult: 1,
                    scanType: ["qrCode"],
                    success: function (res) {
                        alert('?')
                        const mac = res.resultStr
                        const position = 3
                        const openid = this.data.openid
                        post(BIND, {mac, position, openid}, () => {
                            Toast.success("绑定成功")
                        })
                    }
                });
            }
        },
        created() {
            const data = store.fetch()
            this.initConfig()
            if (data && data.hasOwnProperty("openid")) {
                // has open id?
                this.data = data
                if (data.hasOwnProperty("id")) {
                    // init config.
                } else {
                    const params = {openId: data.openid}
                    // is bind?
                    post(DRIVER_VERIFY, params, res => {
                            let resData = res;
                            resData.openid = data.openid
                            store.save(resData)
                            this.data = resData
                        }, () => this.$router.push({name: 'bind'})
                    );
                }
            } else {
                const code = this.$route.query.code
                if (code) {
                    const params = {code}
                    get(FETCH_OPENID, params, res => {
                        this.data.openid = res.openid;
                        store.save(this.data)
                        post(DRIVER_VERIFY, params, res => {
                                let resData = res;
                                resData.openid = data.openid
                                store.save(resData)
                                this.initConfig()
                            }, () => this.$router.push({name: 'bind'})
                        );
                    })
                    ;
                } else {
                    Toast.fail("无法获得授权信息，请退出重试")
                }
            }
        }
    }
</script>
